import axios from 'axios'
import { Message, MessageBox } from 'element-ui'
import store from '@/store'
import { getToken } from '@/utils/auth'
import router from '../routes/router'
// create an axios instance
const service = axios.create({
  // baseURL: 'http://192.168.0.134:8089', // apibase_url
  baseURL: process.env.BASE_API, // apibase_url
  timeout: 500000, // request timeout
  //cookie
})


// request interceptor
service.interceptors.request.use(config => {
  // console.log(1)
  // console.log(store.getters.access_token)
  // Do something before request is sent

  if (store.getters.token) {
    // config.headers['Vue-Token'] = getToken() // token-- ['X-Token']key 
    config.headers['Authorization'] = 'Bearer ' + getToken() // token-- ['X-Token']key 
  }
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone interceptor
service.interceptors.response.use(
  response => {
    return response
  },
  /**
  * responsecodecode
  * xmlhttprequest  error
  */
  //   const res = response.data;
  //   if (res.code === "200") {
  //     console.log("")
  //     this.$router.push({
  //       redirect: '/login'
  //     });
  //   }
  //   return response
  // },
  //       // 50008:token; 50012:;  50014:Token ;
  //       if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
  //         MessageBox.confirm('', '', {
  //           confirmButtonText: '',
  //           cancelButtonText: '',
  //           type: 'warning'
  //         }).then(() => {
  //           store.dispatch('FedLogOut').then(() => {
  //             location.reload();// vue-router bug
  //           });
  //         })
  //       }
  //       return Promise.reject('error');
  //     } else {
  //       return response.data;
  //     }
  error => {
    if (error.response) {
      switch (error.response.status) {
        case 401:
          router.replace({
            path: 'login'
          })
      }
      console.log('err' + error)// for debug
      Message({
        message: error.message,
        type: 'error',
        duration: 5 * 1000
      })
      return Promise.reject(error)
    }
  })

export default service


